:root{
  --defwidth:700px;

}

.App {
  max-width: var(--defwidth);
  margin-left: auto;
  margin-right: auto;

  height: 100vh;
  max-height: 100vh;
  min-height: 100vh;
  
  background-color: black;
  color:white;
  font-size: 18px;

  display:grid;
  grid-template-rows: 70%  10% 20%;
  flex-shrink: 1;

  animation: appborder 4sec;
}

@keyframes appborder {
  0%{
    border: 1px red;
  }
  25%{
    border: 1px rgb(55, 0, 255); 
  }
  50%{
    border :1px rgb(179, 255, 0);
  }
  75%{
    border : 1pxpx rgb(255, 0, 234);
  }
  100%{
    box-shadow:1px red;
  }
}

header{
  position: fixed;
  top: 0px;
  background: linear-gradient(to bottom, #000000bd, #00000052, transparent);
  height: 8vh;
  z-index: 20;
  max-width: var(--defwidth);
  width: var(--defwidth);
  margin-left: auto;
  margin-right: auto;
}

