.ChatDisplayArea{
  grid-row: 1/2;
  position: relative;
}

.textDisplayArea{
  display: flex;
}

.textContainer{
  width: 100%;
  padding: 15px 20px;
}


.textDisplayArea > image{
  width: 30%;
}

.textContainer > input[type = "text"]{
  width: 100%;
  height: 100%;
  border: none;
  color:white;
  font-size: 16px;
  border-radius: 10px;
  background-color: rgb(1, 1, 1);
}


.blur{
  height: 5vh;
  background: linear-gradient(to top ,rgba(0, 0, 0, 0.018), rgba(0, 0, 0, 0.493), transparent);
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.visualsDisplayArea{
  display: flex;
  justify-content: center;
  align-items: center;
  grid-row: 3/4;
  padding-top: 2px;
  overflow: hidden;
}


img{
  margin-left: auto;
  margin-right: auto;
  height: 0%;
  width: auto;
  transform: translateY(-5%);
  transition: all 1sec ease-in-out;
}

.orb{
  mix-blend-mode:hard-light;
  height: 100%;
}




#linespectrum{
  height: 150%;
  transform: translateY(-5%);
}